@charset "UTF-8";
@font-face {
  font-family: "TT Lakes Regular";
  src: url(./fonts/TTLakes-Regular.ttf);
}
@font-face {
  font-family: "TT Lakes Medium";
  src: url(./fonts/TTLakes-Medium.ttf);
}
@font-face {
  font-family: "TT Lakes Bold";
  src: url(./fonts/TTLakes-Bold.ttf);
}
body {
  width: 320px;
  margin: 0 auto;
}

main {
  background-color: #f8f8f8;
  font-family: "TT Lakes Regular", serif;
  padding: 0 0 0 16px;
  margin: 0;
}

h1 {
  margin: 0;
}

h2 {
  color: #7e7e82;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  text-transform: uppercase;
  box-sizing: border-box;
  margin-left: -16px;
  padding-right: 55px;
  padding: 16px;
  background-image: linear-gradient(#f8f8f8, #ffffff);
  border-top: 1px solid #d9fff5;
}

footer {
  background-color: #f8f8f8;
  font-family: "TT Lakes Medium", serif;
  color: #7e7e82;
  font-size: 14px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 16px;
}
footer span {
  display: inline-block;
}
footer a {
  display: inline-block;
  color: #7e7e82;
}

a {
  text-decoration: none;
}

@media screen and (min-width: 767px) {
  body {
    width: 768px;
  }
  main {
    padding-left: 24px;
  }
  h2 {
    margin-left: -24px;
  }
  footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  footer a {
    flex-grow: 1;
    flex-basis: 0;
  }
  footer span {
    flex-grow: 1;
    flex-basis: 0;
  }
  footer span:first-child {
    text-align: left;
  }
  footer span:last-child {
    text-align: right;
  }
}
@media screen and (min-width: 1440px) {
  body {
    width: 1440px;
  }
  .main-page__wrapper {
    width: 1120px;
  }
  main {
    padding-left: 34px;
  }
  h2 {
    margin-left: -34px;
  }
}
button {
  background: #ff3e79 no-repeat center;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  border: none;
  cursor: pointer;
}
button:hover {
  opacity: 0.5;
  transition: 0.1s;
}

.top-menu__button--burger {
  background-image: url(./static/iconburger.svg);
}

.top-menu__button--call {
  background-image: url(./static/call.svg);
}

.top-menu__button--chat {
  background-image: url(./static/chat.svg);
}

.top-menu__button--profile {
  background-image: url(./static/profile.svg);
}

.top-menu__button--repair {
  background-image: url(./static/repair.svg);
}

.top-menu__button--checkstatus {
  background-image: url(./static/checkstatus.svg);
}

.main-info__desktop-button--repair {
  background-image: url(./static/repair.svg);
  position: relative;
}
.main-info__desktop-button--repair::before {
  content: "Оставить заявку";
  width: 128px;
  position: absolute;
  transform: translate(-155px, -10px);
}

.main-info__desktop-button--checkstatus {
  background-image: url(./static/checkstatus.svg);
}
.main-info__desktop-button--checkstatus::before {
  content: "Статус ремонта";
  width: 128px;
  position: absolute;
  transform: translate(-155px, -10px);
}

.expand-button {
  background-color: transparent;
  width: 132px;
  height: 24px;
  border-radius: 0;
  font-family: "TT Lakes Medium";
  font-size: 16px;
}
.expand-button::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 11px;
  background: url(./static/expand.svg) no-repeat center;
  transform: translate(-15px, 3px);
}

.expand-button--opened::before {
  transform: translate(-15px, 3px) rotate(180deg);
}

.top-menu {
  width: 320px;
  height: 90px;
  margin: 0;
  border-bottom: 1px solid #d9fff5;
  box-sizing: border-box;
  padding: 18px 16px;
  display: flex;
  gap: 16px;
  align-items: center;
}

.top-menu__button--repair {
  margin-left: 22px;
}
.top-menu__button--repair::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 32px;
  background: url(./static/divider.svg) no-repeat center;
  transform: translate(-41px, -15px);
}

.tablet-button {
  display: none;
}

@media screen and (min-width: 767px) {
  .top-menu {
    width: 768px;
  }
  .tablet-button {
    display: inline-block;
  }
  .top-menu__button--burger {
    margin-right: 22px;
  }
  .top-menu__button--burger::after {
    content: "";
    position: absolute;
    display: inline-block;
    width: 2px;
    height: 32px;
    background: url(./static/divider.svg) no-repeat center;
    transform: translate(40px, -15px);
  }
  .top-menu__button--call {
    margin-left: 250px;
  }
}
@media screen and (min-width: 1440px) {
  header {
    display: none;
  }
}
.main-info__header {
