  height: 40px;
  letter-spacing: -0.6px;
  padding-top: 24px;
  position: relative;
}
.main-info__header::before {
  content: "";
  position: absolute;
  background-image: url(./static/highlight.svg);
  width: 4px;
  height: 40px;
