}

.main-info__menu {
  list-style-type: none;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  align-items: center;
  padding: 0;
  font-family: "TT Lakes Medium", serif;
}
.main-info__menu::-webkit-scrollbar {
