}

.main-info__text {
  line-height: 18px;
  padding-right: 16px;
}

.company-image {
  width: 320px;
  height: 176px;
  margin-left: -16px;
  padding: 16px 0 24px;
}

@media screen and (min-width: 767px) {
  .main-info__content {
    display: inline-block;
    width: 312px;
    margin-right: 40px;
    vertical-align: top;
  }
  .main-info__content .text--short {
    height: 160px;
  }
  .company-image {
    width: 360px;
    height: 208px;
  }
  .main-info__header::before {
    left: -24px;
  }
}
@media screen and (min-width: 1440px) {
  .main-info {
    padding-top: 18px;
  }
  .main-info__menu {
    overflow: unset;
    flex-wrap: wrap;
  }
  .main-info__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 34px;
  }
  .main-info__desktop-menu {
    display: flex;
    gap: 160px;
    justify-content: flex-end;
    font-family: "TT Lakes Medium", serif;
    font-size: 16px;
  }
  .main-info__header::before {
    left: -34px;
  }
  .company-image {
    width: 532px;
    height: 308px;
  }
  .main-info__content {
    width: 490px;
  }
  .main-info__content .text--short {
    height: 160px;
  }
}
.swiper {
  width: 304px;
}

.swiper-wrapper {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.swiper-slide a {
  display: flex;
  box-sizing: border-box;
  background-color: white;
  border-radius: 8px;
  border: 1px solid #eaeaea;
  align-items: center;
  padding: 0 16px 0 16px;
  justify-content: space-between;
}

.swiper-slide a::after {
  content: "";
  background: url(./static/dropdown.svg) no-repeat center;
  border: 2px solid #ff3e79;
  border-radius: 100%;
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
}

.swiper-slide a:hover::after {
  transition: 0.1s;
  opacity: 0.7;
}

.swiper::after {
  content: "";
  display: inline-block;
  position: absolute;
  z-index: 10;
  width: 24px;
  height: 76px;
  top: -5px;
  right: -8px;
  background-image: linear-gradient(rgba(248, 248, 248, 0.5), rgba(255, 255, 255, 0.8));
  -webkit-filter: blur(3px);
          filter: blur(3px);
}

.swiper-pagination {
  display: flex;
  position: static;
  gap: 2px;
}

.swiper-pagination-bullet {
  background-color: #b5b6bc;
  width: 12px;
  height: 12px;
}

.brands-info {
  padding: 0 0 16px;
}
.brands-info .swiper-slide a {
  width: 240px;
  height: 72px;
}
.brands-info .swiper-pagination {
  margin-left: 21px;
  margin-top: 15px;
}
@media screen and (max-width: 767px) {
  .brands-info button {
    display: none;
  }
}

@media screen and (min-width: 767px) {
  .brands-info .swiper {
    display: block;
    width: 720px;
  }
  .brands-info .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    row-gap: 16px;
    -moz-column-gap: 23px;
         column-gap: 23px;
  }
  .brands-info .swiper-wrapper--short {
    overflow: hidden;
    height: 160px;
  }
  .brands-info .swiper-slide a {
    width: 224px;
  }
  .brands-info .swiper::after {
    display: none;
  }
}
@media screen and (min-width: 1440px) {
  .brands-info .swiper {
    width: 1120px;
  }
  .brands-info .swiper-wrapper {
    -moz-column-gap: 32px;
         column-gap: 32px;
  }
  .brands-info .swiper-slide a {
    width: 240px;
  }
}
.features-info .swiper-slide a {
  width: 240px;
  height: 160px;
  font-family: "TT Lakes Medium";
  text-decoration: none;
  color: #1b1c21;
  align-items: flex-start;
  padding: 16px;
}
.features-info .swiper-slide a::after {
  bottom: -85px;
}
.features-info .swiper-pagination {
  display: block;
  margin-top: 15px;
}
.features-info .swiper::after {
  height: 160px;
}
@media screen and (max-width: 767px) {
  .features-info button {
    display: none;
  }
}

@media screen and (min-width: 767px) {
  .features-info .swiper {
    display: block;
    width: 720px;
  }
  .features-info .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    row-gap: 16px;
    -moz-column-gap: 23px;
         column-gap: 23px;
  }
  .features-info .swiper-wrapper--short {
    overflow: hidden;
    height: 160px;
  }
  .features-info .swiper-slide a {
    width: 224px;
  }
  .features-info .swiper::after {
    display: none;
  }
}
@media screen and (min-width: 1440px) {
  .features-info .swiper {
    width: 1120px;
  }
  .features-info .swiper-wrapper {
    row-gap: 16px;
    -moz-column-gap: 32px;
         column-gap: 32px;
  }
  .features-info .swiper-slide a {
    width: 240px;
  }
}
@media screen and (max-width: 767px) {
  .swiper-slide--mobile {
    display: none;
  }
}
.price-info {
  border-bottom: 1px solid #d9fff5;
  padding-bottom: 40px;
}
.price-info a {
  width: 200px;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  letter-spacing: -0.2px;
  color: #1b1c21;
  font-family: "TT Lakes Medium";
}
.price-info a::after {
  content: "";
  position: absolute;
  width: 26px;
  height: 18px;
  background: url(./static/dropdown.svg) no-repeat center;
}
.price-info p {
  font-size: 14px;
  padding-right: 25px;
}
.price-info .swiper::after {
  height: 200px;
}
.price-info .swiper-pagination {
  display: block;
  margin-top: 16px;
}

.price-card {
  width: 260px;
  height: 200px;
  background-color: #ffffff;
  border-radius: 6px;
  padding: 8px 16px 8px;
  box-sizing: border-box;
  align-items: center;
  display: grid;
  grid-template-columns: auto 120px;
  grid-template-areas: "description description" "price price" "time button";
}
.price-card button {
  grid-area: button;
  border-radius: 16px;
  width: 120px;
  height: 32px;
  padding-top: 2px;
  padding-left: 14px;
  align-self: end;
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
  text-align: start;
  font-weight: 700;
}
.price-card button::after {
  content: "";
  position: absolute;
  width: 7px;
  height: 12px;
  background: url(./static/dropdownwhite.svg) no-repeat center;
  margin-left: 25px;
}

.price-card__description {
  grid-area: description;
}
.price-card__description::before {
  content: "Ремонтные услуги";
  display: block;
  color: #7e7e82;
  font-family: "TT Lakes Medium";
  height: 20px;
}

.price-card__price {
  grid-area: price;
}
.price-card__price::before {
  content: "Цена";
  display: block;
  color: #7e7e82;
  font-family: "TT Lakes Medium";
  height: 20px;
}

.price-card__time {
  grid-area: time;
}
.price-card__time::before {
  content: "Срок";
  display: block;
  color: #7e7e82;
  font-family: "TT Lakes Medium";
  height: 20px;
}

@media screen and (min-width: 767px) {
  .price-card__description::before {
    display: none;
  }
  .price-card__price::before {
    display: none;
  }
  .price-card__time::before {
    display: none;
  }
  .swiper-slide:nth-child(2) .price-card {
    border-radius: 16px 16px 0 0;
  }
  .swiper-slide:last-child .price-card {
    border-radius: 0 0 16px 16px;
  }
  .price-card {
    width: 720px;
    height: 65px;
    grid-template-columns: auto 100px 100px 120px;
    grid-template-areas: "description price time button";
    border-radius: 0;
    position: relative;
  }
  .price-card::after {
    content: "";
    position: absolute;
    bottom: 0;
    display: block;
    width: 688px;
    height: 1px;
    margin: 0 16px;
    background-color: #eaeaea;
  }
  .price-card button {
    align-self: center;
  }
  .swiper-slide--mobile .price-card {
    background-color: #f8f8f8;
    color: #7e7e82;
    font-family: "TT Lakes Medium", serif;
  }
  .price-info .swiper-slide:first-child .price-card::after,
  .price-info .swiper-slide:last-child .price-card::after {
    display: none;
  }
  .price-info p {
    width: 421px;
  }
  .price-info a {
    width: 400px;
  }
}
@media screen and (min-width: 1440px) {
  .price-card {
    width: 1050px;
    grid-template-columns: auto 210px 210px 120px;
  }
  .price-card::after {
    width: 1008px;
  }
}
.side-menu {
  display: none;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.8);
}

.side-menu__wrapper {
  width: 320px;
  background-color: #ffffff;
  box-sizing: border-box;
  margin: 0;
  margin-right: auto;
  padding: 19px 24px;
  font-family: "TT Lakes Medium", serif;
  font-size: 16px;
  color: #1b1c21;
  box-shadow: 5px -15px 55px -15px grey;
}

.side-menu--opened {
  display: block;
  position: fixed;
  z-index: 10;
}

.side-menu__main {
  margin-bottom: 0px;
}

.side-menu__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 17px;
  height: 52px;
  margin-bottom: 46px;
}

.side-menu__top-button--cross {
  background-image: url(./static/cross.svg);
}
@media screen and (min-width: 1440px) {
  .side-menu__top-button--cross {
    display: none;
  }
}

.side-menu__top-button--search {
  background-image: url(./static/search.svg);
  margin-left: 50px;
}

.side-menu__main {
  list-style-type: none;
  padding: 0;
}
.side-menu__main li {
  position: relative;
}

.side-menu__main > li > a {
  display: block;
  height: 24px;
  margin-bottom: 32px;
}

.side-menu__main > li > a:hover {
  transition-property: opacity;
  transition-duration: 0.4s;
  opacity: 0.5;
}

.side-menu__main > li:hover::before {
  content: "";
  display: block;
  position: absolute;
  left: -25px;
  transform: translateY(-6px);
  width: 6px;
  height: 36px;
  background: url(./static/highlight.svg) no-repeat center;
}

.side-menu a {
  color: #1b1c21;
}

.side-menu__bottom {
  margin-bottom: 40px;
}

.side-menu__bottom-buttons {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.side-menu__bottom-button--call {
  background-image: url(./static/call.svg);
}

.side-menu__bottom-button--chat {
  background-image: url(./static/chat.svg);
}

.side-menu__bottom-button--profile {
  background-image: url(./static/profile.svg);
}

.side-menu__bottom-contacts a {
  display: block;
}

.side-menu__bottom-contacts--number {
  font-family: "TT Lakes Bold", serif;
  font-size: 25px;
  margin-top: 12px;
}

.language {
  list-style-type: none;
  display: flex;
  gap: 7px;
  padding: 0;
}
.language .language--current {
  color: #7e7e82;
}

@media screen and (min-width: 767px) {
  .side-menu__main {
    margin-bottom: 236px;
  }
}
@media screen and (min-width: 1440px) {
  .desktop-page__wrapper {
    display: flex;
    flex-direction: row;
  }
  .side-menu {
    display: block;
    position: static;
    z-index: 0;
  }
  .side-menu__wrapper {
    display: block;
    position: static;
    width: 320px;
    box-shadow: none;
  }
}
